import { useContext } from 'react';
import NativeSelect from '@mui/material/NativeSelect';
import MenuIcon from '@mui/icons-material/Menu';
import { LanguageContext } from '../../context/LanguageContext';
import Logo from '../../static/images/logo.png';
import { LANGUAGE_OPTIONS } from '../../static/util/constant';
import {
  HeaderStyled, LogoWrapperStyled, LanguageWrapperStyled,
  NavStyled, MenuStyled, MenuItemStyled, IconButtonStyled,
} from './HeaderStyled';


export default function Navigation({
  menu
}) {
  const { lang } = useContext(LanguageContext);
  return (
    <HeaderStyled>
      <LogoWrapperStyled>
        <div>
          <img src={Logo} alt="Logo" height="70px" />
        </div>
      </LogoWrapperStyled>
      <NavStyled>
        <MenuStyled>
          {menu.map(item => (
            <MenuItemStyled key={item.langKey}>{lang[item.langKey]}</MenuItemStyled>
          ))}
        </MenuStyled>
      </NavStyled>
      <LanguageWrapperStyled>
        <NativeSelect
          defaultValue={LANGUAGE_OPTIONS[0].id}
          inputProps={{
            name: 'age',
            id: 'uncontrolled-native',
          }}
        >
          {LANGUAGE_OPTIONS.map(item => (
            <option key={item.id} value={item.id}>{item.label}</option>
          ))}
         
        </NativeSelect>
      </LanguageWrapperStyled>
      <IconButtonStyled
        color="inherit"
        aria-label="open drawer"
        edge="start"
        onClick={() => {}}
      >
        <MenuIcon />
      </IconButtonStyled>
    </HeaderStyled>
  );
}